import React, { useEffect, useState } from 'react'
import { inject, observer } from 'mobx-react'
import './dip.less'
import Button from '../../components/Button/Button'
import apiProxy from '../../model/ApiProxy'
function DipTwo({ wallet = {}, lang }) {
  const [optionOne, setOptionOne] = useState(false)
  const [optionTwo, setOptionTwo] = useState(false)
  const [optionThree, setOptionThree] = useState(false)
  const [checkedOption, setCheckedOption] = useState('')
  const [userVote, setUserVote] = useState('')
  const [userPower, setUserPower] = useState('')
  const [countVote, setCountVote] = useState([])
  const [sumVote, setSumVote] = useState('')
  const [timestamp, setTimestamp] = useState('')
  const radioChange = (e) => {
    let { value } = e.target
    setCheckedOption(value)
  }
  const voteBtn = async () => {
    if (!checkedOption) {
      alert(lang['please-choose-your-vote-first'])
      return
    }
    let res = await isOk()
    if (res) {
      getUserVote()
      getUserPower()
      getVoting()
    }
  }

  const isOk = async () => {
    let res = await apiProxy.request('vote', [wallet.detail.chainId, wallet.detail.account, checkedOption], { includeResponse: true })
    if (res.success) {
      let bool = true
      while (bool) {
        let data = await apiProxy.request('getUserVotingResult', [wallet.detail.account])
        if (data.timestamp) {
          data.timestamp > timestamp ? bool = false : bool = true
        } else {
          data.timestamp ? bool = false : bool = true
        }
      }
      return true;
    }
  }


  const getUserVote = async () => {
    let res = await apiProxy.request('getUserVotingResult', [wallet.detail.account])
    if (res) {
      if (res.option === '1') {
        setOptionOne(true)
        setUserVote("I")
      } else if (res.option === '2') {
        setOptionTwo(true)
        setUserVote("II")
      } else if (res.option === '3') {
        setOptionThree(true)
        setUserVote("III")
      }
      setTimestamp(res.timestamp)
    }
  }

  const getUserPower = async () => {
    let res = await apiProxy.request('getUserVotingPower', [wallet.detail.account])
    if (res) {
      setUserPower(res)
    }
  }

  const getVoting = async () => {
    let res = await apiProxy.request('getVotingResult', [])
    if (res) {
      let num = +res[0] + (+res[1]) + (+res[2])
      setCountVote(res)
      setSumVote(num)
    } else {
      alert(res.error)
    }
  }

  useEffect(() => {
    let interval = null;
    interval = window.setInterval(() => {
      getVoting()
    }, 1000)
  }, [])
  useEffect(() => {
    let interval = null;
    if (wallet.isConnected()) {
      interval = window.setInterval(() => {
        getUserPower()
        getUserVote()
      }, 1000)
    }
  }, [wallet, wallet.detail.account])
  return (
    <div className='dip-box'>
      <div className='dip-box-info'>
        <div className='dip-info dip-two-info'>
          <div className='dip-info-title'>
            <div className='dip-title'>
              {lang['dip-two']}
            </div>
            <div className='dip-des'>
              <div>{lang['governance-describe']}</div>
              <div>{lang['governance-describe-two']}</div>
              <div>
                {lang['governance-describe-three']} <a target='_blank' href='https://docs.deri.finance/mining-faq#what-do-i-harvest-for-liquidity-mining'  rel='noreferrer' >{lang['governance-describe-three-a']}</a>  {lang['governance-describe-three-one']}
              </div>
            </div>
          </div>
          <div className='dip-options'>
            <div className='dip-options-checked'>
              <div className='checkbox'>
                <input
                  type='radio'
                  name='option'
                  value='1'
                  id="I"
                  onChange={event => radioChange(event)}
                  defaultChecked={optionOne}
                />
                <label for='I'>I.</label>
              </div>
              <div className='option-describe'>
                <div>
                  <div>
                    {lang['governance-options-one-describe']}
                  </div>
                </div>

              </div>
              <div className='peng'>
                <div className='peng-box'>
                  <div className='peng-bg dip-two-one'></div>
                  <div className='peng-num'>
                    142,989 DERI
                  </div>
                </div>
              </div>
            </div>
            <div className='dip-options-checked'>
              <div className='checkbox'>
                <input
                  type='radio'
                  name='option'
                  value='2'
                  id="II"
                  onChange={event => radioChange(event)}
                  defaultChecked={optionTwo}
                />
                <label for='II'>II.</label>
              </div>
              <div className='option-describe'>
                <div>
                  
                  <div>
                    {lang['governance-options-two-describe']}
                  </div>
                </div>

              </div>
              <div className='peng'>
                <div className='peng-box'>
                  <div className='peng-bg dip-two-two'></div>
                  <div className='peng-num'>
                    1,440,582 DERI
                  </div>
                </div>
              </div>
            </div>
            <div className='dip-options-checked'>
              <div className='checkbox'>
                <input
                  type='radio'
                  name='option'
                  value='3'
                  id="III"
                  onChange={event => radioChange(event)}
                  defaultChecked={optionThree}
                />
                <label for='III'>III.</label>
              </div>
              <div className='option-describe'>
                <div>
                  <div>
                    {lang['governance-options-three']}
                  </div>
                </div>

              </div>
              <div className='peng'>
                <div className='peng-box'>
                  <div className='peng-bg dip-two-three'></div>
                  <div className='peng-num'>
                    0 DERI
                  </div>
                </div>
              </div>
            </div>
            <div className='vote-button'>
              <Button label={lang['vote']} width='332' fontSize='18' />
            </div>
          </div>

        </div>
        <div className='voting-rules dip-two-rules'>
          <div className='voting-rules-title'>
            {lang['voting-rules']} :
            </div>
          <div className='voting-info'>
            <div>1. {lang['vote-rules-one']}</div>
            <div>{lang['vote-rules-one-describe-one']}</div>
            <div>{lang['vote-rules-one-describe-two']}</div>
            <div>{lang['vote-rules-one-describe-three']}</div>
            <div>{lang['vote-rules-one-describe-four']}</div>
          </div>
          <div className='voting-info'>
            <div>2.{lang['vote-rules-two']}</div>
          </div>
          <div className='voting-info'>
            <div>3.{lang['vote-rules-three']}</div>
          </div>
          <div className='voting-info'>
            <div>4.{lang['vote-rules-four']}</div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default inject('wallet')(observer(DipTwo))

